*{margin:0;padding:0}body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;--webkit-font-smoothing: antialiased;--moz-osx-font-smoothing: grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.App{text-align:center}main{width:100vw;height:100vh;overflow:hidden}.setting_logo{width:30px;position:absolute;right:10px;top:10px;cursor:pointer}.ss_container{box-sizing:border-box;height:70%;overflow-y:auto;margin:20px;border:1px solid blue}.ss_container .radioStation_btn_container{display:flex;flex-wrap:wrap;justify-content:center;gap:5px;border:1px solid red}.ss_container .radioStation_btn_container div{margin-bottom:5px}.ss_container .radioStation_btn_container button{cursor:pointer;margin-bottom:5px;width:100px;height:100px;padding:5px}.ss_container .radioStation_btn_container button img{width:100%;height:auto}.player{height:30%}